*, html, body{
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img{
  vertical-align: top;
}

.toggleThemeButton{
  cursor: pointer;
  position: fixed;
  top: 0.5rem;
  left: 0.5rem;
  z-index: 9999;
}

.dark-mode{
  filter: invert(1) hue-rotate(180deg);
}

.invert{
  filter: invert(1) hue-rotate(180deg);
}

.navbar{
  height: 7vh;
  display: flex;
  position: fixed;
  left: 0;
  right: 0;
  justify-content: center;
  align-items: center;
  background-color: #f8f8ff;
  z-index: 999;
}

.nav-logo{
  /*display: flex;
  justify-content: flex-start;
  padding: 1rem;
  color: black;*/
  width: auto;
  height: auto;
}

.nav-links{
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  width: 50%;
  height: 100%;
}

.nav-links *{
  margin: 0 0.5rem;
}

.nav-links a{
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  color: black;
  height: 100%;
  width: 6rem;
  position: relative;
  text-decoration: none;
}

.nav-link-active::before{
  content: "";
  display: block;
  height: 0.2rem;
  width: 100%;
  background-color: black;
  position: absolute;
  top: 0;
}

.nav-links a:hover::before{
  content: "";
  display: block;
  height: 0.2rem;
  width: 100%;
  background-color: black;
  position: absolute;
  top: 0;
}

.nav-contact-links{
  position: fixed;
  top: 0.5rem;
  right: 0.5rem;
  z-index: 999;
}

.nav-contact-links a{
  margin: 0.5rem;
  color: black;
}

.container{
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100%;
  background-color: #f8f8ff;
}

.contact{
  flex-direction: column;
}

.contact-form *{
  display: block;
}

.landing{
  display: flex;
  justify-content: center;

}

.landing h2{
  font-family: 'VT323', monospace;
  font-weight: normal;
}

.landing-img{
  display: flex;
  justify-content: flex-end;
  width: 100%;
  height: 100%;
  margin: 1rem;
}

.landing-img img{
  display: flex;
  justify-content: center;
  width: 20rem;
  height: auto;
}

.landing-intro{
  width: 100%;
  height: 100%;
  margin: 1rem;
}

.projectButton{
  border: none;
  outline: none;
  background-color: white;
  color: black;
  width: 12rem;
  height: 3rem;
  margin: 2rem 0rem;
  border: 5px solid black;
  font-size: 1.5rem;
  transition: 250ms;
  cursor: pointer;
}

.projectButton a, .projectButton a:visited{
  text-decoration: none;
  color: black;
}

.projectButton:hover{
  filter: invert(1) hue-rotate(180deg);
}



.projects{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 7vh;
}

.project-preview{
  display: block;
  width: 20rem;
  height: 25rem;
  margin: 1rem;
  overflow:hidden;
  box-shadow:  15px 15px 29px #d1d1d1a8, 
  -15px -15px 29px #ffffff;
}

.project-preview:hover{
  transform: scale(1.02);
  transition: transform 250ms;
}

.project-image{
  height: 50%;
}

.project-preview img{
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.project-preview h3{
  text-align: center;
  margin-top: 0.5rem;
}

.project-preview .info{
  width: 100%;
  height: 50%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  background-color: white;
  border-top: 1px solid darkgray;
}

.project-preview .info p{
  margin: 0 1rem;
  height: 100%;
}

.project-preview .info h2{
  font-size: 1.17rem;
  margin-top: 0.5rem;
  text-align: center;
}

.divider{
  height: 7vh;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  background-color: black;
  color: white;
}

.project-preview .made-with{
  height: 7vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5rem;
}

.project-preview .made-with p{
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 0.5rem;
}

.project-preview .made-with i{
  margin: 0.3rem;
}

.project-preview .links{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
}

.project-preview .links a{
  margin: 0 0.3rem;
  color: white;
  transition: color 500ms;
}

.project-preview .links a:hover{
  color:rgb(0, 73, 0);
}




.about{
  flex-direction: column;
}

.about p{
  max-width: 40rem;
  margin: 1rem;
}

.audioplayer{
  width: 35rem;
  box-shadow:  15px 15px 29px #d1d1d1, 
             -15px -15px 29px #ffffff;
  outline: 1px solid black;
}

.playlistartcontainer{
  height: 10%;
  width: 100%;
  display: flex;
  background-color: black;
  border-bottom: 1px solid white;
}

.playlistartcontainer .playlist, .playlistartcontainer .albumart{
  height: 100%;
  width: 50%;
}

.albumart{
  overflow: hidden;
}

.albumart img{
  height: 300px;
  width: 300px;
  object-fit: cover;
}

.playlist{
  overflow: auto;
  overflow-x: hidden;
}

.controls{
  height: 7rem;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: black;
  border-top: 1px solid white;
}

.controlsbutton{
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  height: 5rem;
  width: 5rem;
  background-color: inherit;
  border: none;
  cursor: pointer;
  border-radius: 50%;
  transition-duration: 250ms;
  color:white;
}

.controlsbutton:hover{
  box-shadow:  2px 2px 15px #d9d9d9b0, 
  -2px -2px 15px #ffffff3a;

}

.centerplay{
  margin-left: 0.5rem;
}

.centerprevious{
  margin-right: 0.4rem;
}

.centernext{
  margin-left: 0.4rem;
}

.audiotrack{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 3rem;
  position: relative;
  cursor: pointer;
  padding-left: 1rem;
  background-color: white;
}

.audiotrack:nth-child(n+2) {
  border-top: 1px solid  black;
}

.audiotrack:last-child{
  border-bottom: 1px solid  black;
}

.audiotrack:hover{
  background-color: black;
  color: white;
}



.currentlyPlaying::before{
  content: '';
  height: 0.5rem;
  width: 0.5rem;
  background-color: black;
  position: absolute;
  border-radius: 50%;
  left: 1.5%;
}

.musicnavigator{
  background-color: white;
  position: relative;
  width: 100%;
  height: 1rem;
}

.musicnavigator div{
  background-color: black;
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 1rem;
  pointer-events: none;
}

.volume{
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 2rem;
}

.volumeslider{
  width: 90%;
  background: black;
}

.contact-form{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin:  3rem 0;
  padding: 2rem;
  background-color: white;
  box-shadow:  15px 15px 29px #d1d1d1a8, 
  -15px -15px 29px #ffffff;
}

.contact-form label{
  font-weight: bold;
}

.contact-form input, .contact-form textarea{
  margin: 0.25rem 0 1rem 0;
  width: 20rem;
}

.contact-form textarea{
  height: 10rem;
}

@media (max-width: 576px){
  .navbar{
    bottom: 0;
    font-size: 0.8rem;
  }

  .nav-link-active::before{
    top: auto;
    bottom: 0;
  }
  
  .nav-links a:hover::before{
    top: auto;
    bottom: 0;
  }

  .landing{
    flex-direction: column-reverse;
  }

  .landing div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin: 1rem;
  }

  .landing-img img{
    display: none;
  }

  .projects{
    width: 90%;
    margin-bottom: 2rem;
  }

  .audioplayer{
    width: 100%;
  }

  .contact-form{
    width: 90%;
  }
} 

